import React from 'react';
import PropTypes from 'prop-types';
import Link from '../Link';
import { getPathname } from '@webui-one/core/utils/browser';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import style from './FooterStyles';

class Footer extends React.Component {
    getLink(app) {
        const { hasChanges, LinkComponent } = this.props;
        const native = this.isNativeApplication(app);
        const location = getPathname();
        const abbreviation = (<div style={style.abbreviation}>{
            app.id === 'Commit manager' && hasChanges
                ? `${app.abbreviation}*`
                : app.abbreviation
        }</div>);
        const name = <div style={style.name}>{app.title}</div>;
        if (!isNonEmptyString(app.href)) {
            return false;
        }
        return native ?
            <LinkComponent
                data-handle={`${app.title}Shortcut`}
                key={app.title}
                to={app.href}
                style={
                    location.indexOf(app.href) >= 0 ?
                    style.shortcutItemActive
                    : style.shortcutItem
                }
                >
                {abbreviation}
                {name}
            </LinkComponent>
            : <a
                data-handle={`${app.title}Shortcut`}
                key={app.title}
                href={app.href}
                style={
                    location.indexOf(app.href) >= 0 ?
                    style.shortcutItemActive
                    : style.shortcutItem
                }
                >
                {abbreviation}
                {name}
            </a>;
    }

    isNativeApplication(app) {
        const { isNativeApplication } = this.props;
        if (typeof isNativeApplication === 'function') {
            return isNativeApplication(app);
        }
        return true;
    }

    render() {
        const {
            applications,
            onToggleMenu,
            shortcutPanelIsVisible,
        } = this.props;
        return (
            <div data-handle="footer" style={style.footer}>
                <button
                    style={shortcutPanelIsVisible ? style.buttonSlim : style.buttonFat}
                    onClick={onToggleMenu}
                    key="1"
                    data-handle={
                        `showShortcutsButton${shortcutPanelIsVisible ? 'Disabled' : ''}`
                    }
                    disabled={shortcutPanelIsVisible}
                    >
                    <div
                        style={style.arrow}
                    >⌃</div>
                </button>
                <div style={shortcutPanelIsVisible ? style.containerUp : style.containerDown}>
                    <button
                        style={style.spacer}
                        onClick={onToggleMenu}
                        key="2"
                        disabled={!shortcutPanelIsVisible}
                    />
                    {Object.keys(applications).map((app) => {
                      if (applications[app].shortcut) return this.getLink(applications[app]);
                      return false;
                    })}
                    <button
                        style={style.spacer}
                        onClick={onToggleMenu}
                        key="3"
                        disabled={!shortcutPanelIsVisible}
                        data-handle={
                            `hideShortcutsButton${shortcutPanelIsVisible ? '' : 'Disabled'}`
                        }
                    >
                        <div style={style.arrowDown} >⌃</div>
                    </button>
                </div>
            </div>
        );
    }
}

Footer.propTypes = {
    applications: PropTypes.shape({}),
    hasChanges: PropTypes.bool,
    isNativeApplication: PropTypes.func,
    LinkComponent: PropTypes.elementType,
    onToggleMenu: PropTypes.func,
    shortcutPanelIsVisible: PropTypes.bool,
};

Footer.defaultProps = {
    LinkComponent: Link,
};

export default Footer;

export {
    Footer as _Footer,
};
