import React from 'react';
import PropTypes from 'prop-types';
import style from './DropdownStyles';
import cssStyles from './DropdownStyles.css';

const Dropdown = ({
    dropdownIsVisible,
    title,
    items,
    onHide,
    onKeyDown,
    onShow,
    children,
}) => {
    const dropdownContentItems = items.map(item => (
        <a
            data-handle={`dropdown-content-item-${item.name}`}
            role="button"
            tabIndex="0"
            className={cssStyles.dropdownContentItems}
            key={item.name}
            onClick={() => {
                onHide();
                item.onClick();
            }}
            >{item.name}
        </a>
    ));

    return (<div style={{ position: 'relative' }}>
        <button
            key="two"
            data-handle="toggle-dropdown"
            style={style.dropdownHeader}
            tabIndex="0"
            onClick={onShow}
            onKeyDown={onKeyDown}
            >
            {title} ▾
        </button>
        {dropdownIsVisible && <div style={style.wrapper}>
            <button onClick={onHide} style={style.overlay} />
            <div style={style.menuArrowShadow} />
            <div style={style.dropdown}>
                { dropdownContentItems }
                { children }
            </div>
            <div style={style.menuArrow} />
            </div>}
    </div>);
};

Dropdown.propTypes = {
    children: PropTypes.oneOfType([
        PropTypes.element,
        PropTypes.bool,
    ]),
    dropdownIsVisible: PropTypes.bool,
    items: PropTypes.arrayOf(PropTypes.shape({
        onClick: PropTypes.func,
        name: PropTypes.name,
    })),
    onShow: PropTypes.func,
    onKeyDown: PropTypes.func,
    onHide: PropTypes.func,
    title: PropTypes.string.isRequired,
};

export default Dropdown;
