/* eslint-disable jsx-a11y/no-static-element-interactions */
import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Modal from '@webui-one/components/Modal/Modal';
import ToolTip from '@webui-one/components/ToolTip/ToolTip';
import { getAvailableFlags } from 'modules/flags/flags';
import Select from '@webui-one/components/ConfigRender/Select/Select';
import { selectFlags } from './selectors';
import style from './DevFlagsButtonStyles';
import Flag from './Flag';

import {
    clearFlags,
    setFlag,
    unsetFlag,
} from './actions';


class DevFlagsButtonContainer extends React.Component {
    constructor(props) {
        super(props);
        this.showTooltip = this.showTooltip.bind(this);
        this.hideTooltip = this.hideTooltip.bind(this);
        this.showModal = this.showModal.bind(this);
        this.onClose = this.onClose.bind(this);
        this.onClear = this.onClear.bind(this);
        this.setFlag = this.setFlag.bind(this);
        this.flagTips = {};
        this.state = { showModal: false };
    }

    onClose() {
        this.setState({ showModal: false });
    }

    onClear() {
        this.props.onClearFlags();
    }

    getFlags() {
        const { availableFlags, flags } = this.props;
        const available = Array.isArray(availableFlags) ? availableFlags : [];
        const children = available.map((flag) => {
            if (Array.isArray(flag.values)) {
                return (
                    <div style={style.row} key={flag.name}>
                        <div style={style.row.title}>{flag.title}</div>
                        <Select
                            editable
                            key={flag.name}
                            name={flag.name}
                            options={['', ...flag.values]}
                            onSetValue={value => this.setFlag(flag.name, value)}
                            styleName="listStyles"
                            value={flags[flag.name]}
                        />
                    </div>
                );
            }
            return null;
        });
        return (
            <div>
                {children}
            </div>
        );
    }

    setFlag(name, value) {
        if (!value) {
            this.props.onUnsetFlag(name);
        } else {
            this.props.onSetFlag(name, value);
        }
    }

    showModal() {
        this.setState({ showModal: true });
    }

    hideTooltip() {
        this.tooltip.hide();
    }

    showTooltip() {
        this.tooltip.show();
    }

    render() {
        const { showModal } = this.state;
        return (<div style={style.devFlagButton.container}>
            <button
                style={style.devFlagButton.button}
                onClick={this.showModal}
                onMouseDown={this.showTooltip}
                onMouseUp={this.hideTooltip}
                onMouseEnter={this.showTooltip}
                onMouseLeave={this.hideTooltip}
                data-handle="devFlagsButton"
            >
                <div style={{ width: '1rem', height: '1rem', margin: '0 .25rem', alignSelf: 'center' }}>
                    <Flag />
                </div>
            </button>
            <ToolTip
                direction="left"
                toolTipText="set development flags"
                ref={(tt) => { this.tooltip = tt; }}
            />
            {showModal && <Modal
                title="Flags"
                onRequestClose={this.onClose}
            >
                {this.getFlags()}
                <button
                    data-handle="clearBtn"
                    key="clear"
                    style={style.buttonOk}
                    onClick={this.onClear}
                >Clear</button>
                <button
                    data-handle="closeBtn"
                    key="close"
                    style={style.buttonOk}
                    onClick={this.onClose}
                >Close</button>
            </Modal>}
        </div>);
    }
}

const availableFlagPropType = PropTypes.shape({
    name: PropTypes.string,
    title: PropTypes.string,
});


DevFlagsButtonContainer.propTypes = {
    availableFlags: PropTypes.arrayOf(availableFlagPropType),
    flags: PropTypes.shape({}),
    onClearFlags: PropTypes.func,
    onSetFlag: PropTypes.func,
    onUnsetFlag: PropTypes.func,
};

const mapStateToProps = state => ({
    availableFlags: getAvailableFlags(),
    flags: selectFlags(state),
});

const mapDispatchToProps = dispatch => ({
    onSetFlag(flagName, value) {
        dispatch(setFlag(flagName, value));
    },
    onUnsetFlag(flagName) {
        dispatch(unsetFlag(flagName));
    },
    onClearFlags() {
        dispatch(clearFlags());
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(DevFlagsButtonContainer);

export {
    DevFlagsButtonContainer as _DevFlagsButtonContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
