import {
    createSelector,
} from 'reselect';
import * as S from '@webui-one/core/schema';
import { ensureNumber } from '@webui-one/core/utils/number';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import { pathOr } from '@webui-one/core/utils/object';
import {
    getActionStatus,
    selectYangActions,
    getCheckInSyncStatus,
    getRedeployStatus,
    getRedeployDryRunStatus,
} from 'modules/yangActions/selectors';
import {
    selectNodes,
    getRowKeypath,
} from 'modules/nodes/selectors';
import {
    selectSchema as _selectSchema,
} from 'modules/schemas/reducer';
import { getLinkWithPath } from 'ConfigurationEditor';
import {
    SERVICE_ACTIONS,
    SERVICE_MANAGER_USER_STORAGE_PATH,
} from './paths';
import {
    createColumnsVisibilitySelector,
} from 'modules/userdata/selectors';

const EMPTY_OBJECT = Object.freeze({});
export const servicesSelector = state => state.services || EMPTY_OBJECT;
export const selectServicesMetaData = state => servicesSelector(state).services || EMPTY_OBJECT;
export const servicePointsSelector = state => servicesSelector(state).servicePoints || EMPTY_OBJECT;

export const selectServicePointsKeypaths = state => (
    Object.values(servicePointsSelector(state))
);

export const selectServicePoint = (_, ownPath) => {
    const { path } = ownPath || {};
    return path || '';
};

export const selectLastService = state => servicesSelector(state).lastService;

const isValidServicePoint = (sp, servicePoint, nodes, servicePoints) => (
    ((sp === servicePoint || !isNonEmptyString(servicePoint)) && nodes[servicePoints[servicePoint]])
);

export const selectServiceNodes = createSelector(
    servicePointsSelector,
    selectNodes,
    selectServicePoint,
    (servicePoints, nodes, servicePoint) => (
        Object.keys(servicePoints)
            .reduce((acc, sp) => {
                if (!isValidServicePoint(sp, servicePoint, nodes, servicePoints)) {
                    return acc;
                }
                return {
                    ...acc,
                    [servicePoints[sp]]: nodes[servicePoints[sp]],
                };
            }, {})
    ),
);

export const selectAllServiceNodes = createSelector(
    servicePointsSelector,
    selectNodes,
    (servicePoints, nodes) => (
        Object.keys(servicePoints)
            .reduce((acc, sp) => ({
                ...acc,
                [servicePoints[sp]]: nodes[servicePoints[sp]],
            }), {})
    ),
);

export const selectServiceSchemas = createSelector(
    servicePointsSelector,
    state => state,
    (_, props) => props,
    (
        servicePoints,
        state,
        {
            funcs: { selectSchema = _selectSchema } = {},
        } = {},
    ) => (
        Object.keys(servicePoints).reduce((acc, sp) => (
            { ...acc, [servicePoints[sp]]: selectSchema(state, servicePoints[sp]) }
        ), {})
    ),
);

export const selectCurrentServiceNode = (state, props) => {
    const currentServicePath = selectServicePoint(state, props);
    const nodes = selectServiceNodes(state, props);
    return nodes[currentServicePath] || EMPTY_OBJECT;
};

export const selectExpandedRows = state => servicesSelector(state).expandedRows || EMPTY_OBJECT;
export const selectExpandedRow = (state, path) =>
    selectExpandedRows(state)[path] || EMPTY_OBJECT;
export const isExpanded = expandState => !!(expandState || {}).isExpanded;
export const getExpandedColumnName = expandState => (expandState || {}).column;

export const selectIsExpanded = (state, path) => isExpanded(selectExpandedRows(state, path));

export const selectServiceOffset = (state, props) => (
    selectCurrentServiceNode(state, props).offset || 1
);

export const selectServiceSortColumn = (state, props) => (
    selectCurrentServiceNode(state, props).sort
);

export const selectServiceSortOrder = (state, props) => (
    selectCurrentServiceNode(state, props).sortOrder
);

export const selectTotalNumberOfServices = (state, props) => (
    selectCurrentServiceNode(state, props).totalNumberOfResults || 0
);

export const buildYangActions = (key, yangActions) => SERVICE_ACTIONS.reduce((accAct, action) => ({
    ...accAct,
    [action]: yangActions[`${key}/${action}`],
}), {});

const actionHandlers = {
    're-deploy': getRedeployStatus,
    'check-sync': getCheckInSyncStatus,
    're-deploy dry-run': getRedeployDryRunStatus,
};

const getActionResultHandler = actionKey => actionHandlers[actionKey] || getCheckInSyncStatus;

const getActionsWithStatus = actions =>
    Object.keys(actions).reduce((acc, actionKey) => ({
        ...acc,
        [actionKey]: {
            status: getActionStatus(
                actions,
                actionKey,
                getActionResultHandler(actionKey)).actionStatus,
            yangAction: { ...actions[actionKey], actionStatus: {} },
        },
    }), {});

const noData = (rows, keys) => (
    rows.reduce((missing, row) => (
        row.isLoading || keys.reduce((allKeys, key) => (key in row ? allKeys : true), false)
    ), true)
);

const getIsNodeDirty = node => !!node && (node.isDirty || node.dirtyRefCount > 0);

export const selectServiceIncludesPlan = createSelector(
    selectServiceNodes,
    selectServiceSchemas,
    (serviceNodes, schemas) => {
        const serviceKeys = Object.keys(serviceNodes);
        if (serviceKeys.length === 0) return false;
        const schema = schemas[serviceKeys[0]];
        return !!(schema &&
            schema.children &&
            schema.children.find(c => c.name === 'plan'));
    },
);

export const selectServiceRows = createSelector(
    selectNodes,
    selectServiceNodes,
    selectServiceSchemas,
    selectServicesMetaData,
    selectYangActions,
    selectExpandedRows,
    (nodes, serviceNodes, schemas, metadata, yangActions, expandedRows) =>
        Object.keys(serviceNodes).reduce((acc, k) => {
            const serviceNode = serviceNodes[k];
            const rows = Array.isArray(serviceNode.rows) ? serviceNode.rows : [];
            const schema = schemas[k];
            const keys = S.getKeys(schema);
            const isContainer = S.isContainer(schema);

            if (schema === undefined || noData(rows, keys)) {
                return acc;
            }

            return [...acc, ...rows.map((row) => {
                const name = isContainer
                    ? S.qname(schema)
                    : keys.reduce((stracc, key) => (`${stracc} ${row[key]}`), '');
                const keypath = isContainer
                    ? S.keypath(schema)
                    : getRowKeypath(S.keypath(schema), keys, row);
                const meta = metadata[keypath] || EMPTY_OBJECT;
                const actions = buildYangActions(keypath, yangActions);
                const expandedColumn = getExpandedColumnName(expandedRows[keypath]);
                const actionsWithStatus = getActionsWithStatus(actions);
                const isDirty = getIsNodeDirty(nodes[keypath]);
                if (expandedColumn && actionsWithStatus[expandedColumn]) {
                    actionsWithStatus[expandedColumn].isExpanded =
                        isExpanded(expandedRows[keypath]);
                }

                return ({
                    rowKey: keypath,
                    data: {
                        ...meta,
                        ...actionsWithStatus,
                        name,
                        devices: row['count(device-list)'],
                        expandable: {
                            isExpanded: isExpanded(expandedRows[keypath]),
                            column: expandedColumn,
                            yangAction: { ...(actions || {})[expandedColumn],
                                actionStatus: actionsWithStatus[expandedColumn] },
                        },
                        type: S.name(schema),
                        to: getLinkWithPath(keypath),
                        isDirty,
                    },
                });
            })];
        }, []),
);

export const selectServiceCount = createSelector(
    selectServiceRows,
    rows => rows.length,
);

export const selectSelectedServiceRows = createSelector(
    selectServiceRows,
    rows => rows.filter(r => r.data.isSelected),
);

export const selectSelectedServiceCount = createSelector(
    selectSelectedServiceRows,
    rows => rows.length,
);

export const selectServiceIsLoading = state =>
    !!servicesSelector(state).isLoading;

export const selectServiceInstanceCount = state =>
    servicesSelector(state).instanceCount || {};

export const selectServiceCountByType = (state) => {
    const instances = selectServiceInstanceCount(state);
    const keys = Object.keys(instances);
    return keys
        .reduce((acc, key) => {
            const inst = instances[key];
            if (inst.count > 0) {
                acc.push({
                    name: inst.name,
                    value: inst.count,
                    servicePoint: inst.servicePoint,
                });
            }
            return acc;
        }, [])
        .sort((a, b) => a.name.localeCompare(b.name));
};

export const selectServiceCountTotal = (state) => {
    const instances = selectServiceInstanceCount(state);
    const keys = Object.keys(instances);
    return keys.reduce((acc, key) => acc + instances[key].count, 0);
};

export const selectServiceInstanceCountIsLoading = state => (
    servicesSelector(state).instanceCountIsLoading === true
);

const defaultDeployments = Object.freeze({
    running: 0,
    successful: 0,
    jeopardized: 0,
    violated: 0,
});
export const selectServiceDeployments = (state) => {
    const services = servicesSelector(state);
    return services.deployments || defaultDeployments;
};

export const selectServiceDeploymentCount = (state) => {
    const deployments = selectServiceDeployments(state);
    return ensureNumber(deployments.jeopardized) +
        ensureNumber(deployments.successful) +
        ensureNumber(deployments.running) +
        ensureNumber(deployments.violated);
};

export const selectServiceDeploymentsIsLoading = (state) => {
    const services = servicesSelector(state);
    return !!services.deploymentsIsLoading;
};

export const selectServiceSearch = state => pathOr('', ['services', 'search', 'value'], state);
const userStorageProps = {
    path: SERVICE_MANAGER_USER_STORAGE_PATH,
};

const serviceColumnSelector = createColumnsVisibilitySelector();
export const selectServiceColumns = state => serviceColumnSelector(state, userStorageProps);
