import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import ToolTip from '@webui-one/components/ToolTip/ToolTip';
import {
    registerShortcut,
    unregisterShortcut,
} from 'modules/shortcuts/actions';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import { toggleButton as style } from './ConfigurationEditorStyles';

class ToggleButtonContainer extends React.Component {
    constructor(props) {
        super(props);
        this.showTooltip = this.showTooltip.bind(this);
        this.hideTooltip = this.hideTooltip.bind(this);
        this.shortcut = this.props.shortcut;
        this.shortcutHandler = this.shortcutHandler.bind(this);
    }

    componentDidMount() {
        const { onRegisterShortcut, shortcut } = this.props;
        if (isNonEmptyString(shortcut)) {
            onRegisterShortcut(shortcut, this.shortcutHandler);
        }
    }

    componentWillUnmount() {
        const { onUnregisterShortcut, shortcut } = this.props;
        if (isNonEmptyString(shortcut)) {
            onUnregisterShortcut(shortcut, this.shortcutHandler);
        }
    }

    showTooltip() {
        this.tooltip.show();
    }

    hideTooltip() {
        this.tooltip.hide();
    }

    shortcutHandler(shortcut) {
        const { onToggle } = this.props;

        if (shortcut === this.props.shortcut) {
            onToggle();
        }
    }

    render() {
        const {
            children,
            dataHandle,
            direction,
            isChecked,
            onToggle,
            tooltipText,
        } = this.props;
        const checked = isChecked ? 'checked' : 'unchecked';
        return (<div style={style.container} data-handle={dataHandle}>
            <button
                style={isChecked ? style.button : style.buttonDisabled}
                onClick={onToggle}
                onMouseDown={this.showTooltip}
                onMouseUp={this.hideTooltip}
                onMouseEnter={this.showTooltip}
                onMouseLeave={this.hideTooltip}
                data-checked={checked}
            >
                <div style={style.dot} />
                <div style={style.symbol}>
                    {children}
                </div>
            </button>
            <ToolTip
                direction={direction}
                toolTipText={tooltipText}
                ref={(tt) => { this.tooltip = tt; }}
            />
        </div>);
    }
}

ToggleButtonContainer.propTypes = {
    children: PropTypes.node,
    dataHandle: PropTypes.string,
    direction: PropTypes.string,
    isChecked: PropTypes.bool,
    onRegisterShortcut: PropTypes.func,
    onToggle: PropTypes.func,
    onUnregisterShortcut: PropTypes.func,
    shortcut: PropTypes.string,
    tooltipText: PropTypes.string,
};

ToggleButtonContainer.defaultProps = {
    direction: 'left',
};

const mapDispatchToProps = dispatch => ({
    onRegisterShortcut(shortcuts, handler) {
        dispatch(registerShortcut(shortcuts, handler));
    },
    onUnregisterShortcut(shortcuts, handler) {
        dispatch(unregisterShortcut(shortcuts, handler));
    },
});

export default connect(
    undefined,
    mapDispatchToProps,
)(ToggleButtonContainer);

export {
    ToggleButtonContainer as _ToggleButtonContainer,
};
