import React from 'react';
import PropTypes from 'prop-types';
import Loader from '@webui-one/components/Loading/Loader';
import InformationButton from '@webui-one/components/InformationButton/InformationButton';
import TrashcanSymbol from '@webui-one/styles/static/TrashcanSymbol';
import * as widgetKinds from 'modules/schemas/widgetKinds';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import { style, overlayStyle } from '@webui-one/styles/LeafStyles';

function WidgetLeaf({
    error,
    canBeDeleted,
    children,
    isLoading,
    isMandatory,
    name,
    info,
    isDirty,
    isEditMode,
    isOperdata,
    onDelete,
    isEmptyLeaf,
    exists,
}) {
    let errorMessage = false;

    if (error) {
        if (error.data && error.data.reason) {
            errorMessage = <p data-handle="error" style={style.errorMessage} >{ error.data.reason }</p>;
        } else {
            errorMessage = <p data-handle="error" style={style.errorMessage}>{ error.message }</p>;
        }
    }

    const child = {
        ...children,
        props: {
            ...children.props,
            isLoading,
            error,
        },
    };

    let containerStyles = style.container;

    if (isOperdata) {
        containerStyles = style.containerOperdata;
    } else if (isEmptyLeaf && !exists) {
        containerStyles = style.emptyLeafContainer;
    }

    return (
        <div
            data-handle={name}
            style={containerStyles}
            >
            <div style={style.name} data-handle={isOperdata ? 'operdata' : 'config'}>
                {isMandatory ?
                    <div style={style.center} title="Mandatory attribute">{`${name}*`}</div> :
                    <div style={style.center}>{name}</div>
                }

                {isEditMode &&
                    <div style={overlayStyle.editModeOverlay} data-handle="overlay">
                        {canBeDeleted &&
                            <button
                                style={overlayStyle.button}
                                onClick={onDelete}
                                data-handle="deleteLeaf"
                            >
                                <TrashcanSymbol color={defaultStyles.color1} />
                            </button>
                        }
                        {info.length > 0 &&
                            <div
                                data-handle="informationButton"
                                style={overlayStyle.button}>
                                <InformationButton color={defaultStyles.color1} info={info} />
                            </div>
                        }
                    </div>
                }
            </div>

            { child }

            {isLoading &&
                <div style={style.loadingContainer}>
                    <Loader color={defaultStyles.color6} />
                </div>
            }

            {isDirty && <div style={style.dirtyDot} data-handle="dot" />}

            { children.props.type !== widgetKinds.KIND_CHOICE && errorMessage }
        </div>
    );
}

WidgetLeaf.propTypes = {
    children: PropTypes.shape(Object),
    error: PropTypes.shape(Object),
    canBeDeleted: PropTypes.bool,
    info: PropTypes.arrayOf(PropTypes.object),
    isDirty: PropTypes.bool,
    isEditMode: PropTypes.bool,
    isLoading: PropTypes.bool,
    isOperdata: PropTypes.bool,
    onDelete: PropTypes.func.isRequired,
    name: PropTypes.string,
    isMandatory: PropTypes.bool,
    isEmptyLeaf: PropTypes.bool,
    exists: PropTypes.bool,
};

WidgetLeaf.defaultProps = {
    canBeDeleted: false,
    info: [],
    isDirty: false,
    isEditMode: false,
    isLoading: false,
    isOperdata: false,
    isEmptyLeaf: false,
};

export default WidgetLeaf;
