import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import { selectNumberOfChanges } from 'modules/transactions/selectors';
import {
    logout,
    tryLogout,
    logoutPendingAction,
} from 'modules/logout/actions';
import HelpModal from 'Common/Components/HelpModal/HelpModal';
import DevFlagsButtonContainer from 'modules/flags/DevFlagsButtonContainer';
import { formatPath } from '@webui-one/core/utils/path';
import { isDevEnv } from '@webui-one/core/utils/functional';
import { selectIsPendingLogout } from 'modules/logout/reducer';
import { getApplicationName } from 'modules/application/utils';
import { showHelpAction } from 'modules/application/actions';
import { selectNsoUser, selectNsoVersion } from 'modules/systemSetting/reducer';
import { selectHelpIsVisible } from 'modules/application/selectors';
import Header from '@webui-one/components/Header/Header';

const HeaderContainer = ({
    getCiscoLogoUrl,
    helpIsVisible,
    onShowHelp,
    version,
    ...props
}) => {
    const dropdownItems = [{
        name: 'help',
        onClick: () => onShowHelp(true),
    }];

    const versionText = `NSO VERSION:${version}`;

    return <Fragment>
        <Header
            additionalDropdownComponents={isDevEnv() && <DevFlagsButtonContainer />}
            additionalDropdownItems={dropdownItems}
            ciscoLogoUrl={getCiscoLogoUrl()}
            LinkComponent={Link}
            versionText={versionText}
            {...props}
        />
        {helpIsVisible && <HelpModal onRequestClose={() => onShowHelp(false)} />}
    </Fragment>;
};

HeaderContainer.propTypes = {
    version: PropTypes.string,
    getCiscoLogoUrl: PropTypes.func,
    helpIsVisible: PropTypes.bool,
    onShowHelp: PropTypes.func,
};

HeaderContainer.defaultProps = {
    getCiscoLogoUrl: formatPath,
};

const mapDispatchToProps = dispatch => ({
    onLogoutRequest() {
        dispatch(tryLogout());
    },
    onLogoutConfirm() {
        dispatch(logout());
    },
    onLogoutCancel() {
        dispatch(logoutPendingAction(false));
    },
    onShowHelp(value) {
        dispatch(showHelpAction(value));
    },
});

const mapStateToProps = (state, ownProps) => ({
    applicationName: getApplicationName(state, ownProps),
    numberOfChanges: selectNumberOfChanges(state),
    isPendingLogout: selectIsPendingLogout(state),
    helpIsVisible: selectHelpIsVisible(state),
    user: selectNsoUser(state),
    version: selectNsoVersion(state),
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(HeaderContainer);

export {
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
    HeaderContainer as _HeaderContainer,
};
