import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router';
import { isNativeApplication } from 'Application/Routes';
import { selectHasDirtyState } from 'modules/nodes/selectors';
import { applicationsSelector } from 'modules/applications/selectors';
import { UNSAFEWindowLocationHref } from '@webui-one/core/utils/unsafe';
import { selectShortcutPanelIsVisible } from 'modules/application/selectors';
import { setShowShortcutPanel } from 'modules/application/actions';
import { getTransChangesAsyncAction } from 'modules/transactions/actions';
import {
    registerShortcut,
    unregisterShortcut,
} from 'modules/shortcuts/actions';
import { getApplicationKey } from '@webui-one/core/utils/path';
import { isString } from '@webui-one/core/utils/string';
import { selectLastLocation } from 'ConfigurationEditor/reducer';
import { selectLastService } from 'modules/services/selectors';
import { CONFIGURATION_EDITOR_NAME } from 'ConfigurationEditor';
import { SERVICE_MANAGER_NAME } from 'ServiceManager';
import Footer from '@webui-one/components/Footer/Footer';

const filterApplications = (applications, lastLocation, lastService) => {
    const applicationKey = getApplicationKey(window.location.pathname);
    const applicationPath = isString(lastLocation) ? lastLocation : '';
    const servicePath = isString(lastService) ? lastService : '';
    return Object.keys(applications).reduce((acc, name) => {
        const app = applications[name];
        if (name === 'Configuration editor') {
            const href = applicationKey !== 'ConfigurationEditor'
                ? `/webui-one/${CONFIGURATION_EDITOR_NAME}${applicationPath}`
                : app.href;
            acc[name] = {
                ...app,
                href,
            };
        } else if (name === 'Service manager') {
            const href = applicationKey !== 'ServiceManager'
                ? `/webui-one/${SERVICE_MANAGER_NAME}${servicePath}`
                : app.href;
            acc[name] = {
                ...app,
                href,
            };
        } else {
            acc[name] = app;
        }
        return acc;
    }, {});
};

class FooterContainer extends React.Component {
    constructor(props) {
        super(props);
        this.keyMatches = '';
        this.shortcutHandler = this.shortcutHandler.bind(this);
        this.onToggleMenu = this.onToggleMenu.bind(this);
        this.state = {
            shortcutApplications: filterApplications(
                props.applications,
                props.lastLocation,
                props.lastService,
            ),
        };
    }

    // eslint-disable-next-line camelcase, react/sort-comp
    UNSAFE_componentWillMount() {
        const { applications, onLoad, onRegisterShortcut } = this.props;
        onLoad();
        onRegisterShortcut(
            Object.values(applications).map(app => app.shortcut),
            this.shortcutHandler,
        );
    }

    componentDidUpdate(prevProps) {
        const { applications, lastLocation, lastService } = this.props;
        const {
            applications: prevApplications,
            lastLocation: prevLocation,
            lastService: prevService,
        } = prevProps;
        if (applications !== prevApplications
            || lastLocation !== prevLocation
            || lastService !== prevService
        ) {
            // eslint-disable-next-line react/no-did-update-set-state
            this.setState({
                shortcutApplications: filterApplications(
                    applications,
                    lastLocation,
                    lastService,
                ),
            });
        }
    }

    componentWillUnmount() {
        const { applications, onUnregisterShortcut } = this.props;
        onUnregisterShortcut(
            Object.values(applications).map(app => app.shortcut),
            this.shortcutHandler,
        );
    }

    onToggleMenu() {
        const { onSetShortcutPanel, shortcutPanelIsVisible } = this.props;
        onSetShortcutPanel(!shortcutPanelIsVisible);
    }

    shortcutHandler(s) {
        const { router } = this.props;
        const { shortcutApplications } = this.state;

        Object.values(shortcutApplications).some(({ href, shortcut }) => {
            if (s === shortcut) {
                if (isNativeApplication({ href })) {
                    router.push(href);
                } else {
                    UNSAFEWindowLocationHref(href);
                }
            }
            return s === shortcut;
        });
    }

    render() {
        const {
            hasChanges,
            shortcutPanelIsVisible,
        } = this.props;
        const { shortcutApplications } = this.state;
        return (
            <Footer
                applications={shortcutApplications}
                hasChanges={hasChanges}
                isNativeApplication={isNativeApplication}
                LinkComponent={Link}
                onToggleMenu={this.onToggleMenu}
                shortcutHandler={this.shortcutHandler}
                shortcutPanelIsVisible={shortcutPanelIsVisible}
            />
        );
    }
}

FooterContainer.propTypes = {
    applications: PropTypes.shape({}),
    hasChanges: PropTypes.bool,
    lastLocation: PropTypes.string,
    lastService: PropTypes.string,
    onLoad: PropTypes.func,
    onRegisterShortcut: PropTypes.func,
    onSetShortcutPanel: PropTypes.func,
    onUnregisterShortcut: PropTypes.func,
    router: PropTypes.shape({ push: PropTypes.func }),
    shortcutPanelIsVisible: PropTypes.bool,
};

FooterContainer.defaultProps = {
    changes: undefined,
};
const mapDispatchToProps = dispatch => ({
    onLoad() {
        return dispatch(getTransChangesAsyncAction());
    },
    onRegisterShortcut(shortcuts, handler) {
        dispatch(registerShortcut(shortcuts, handler));
    },
    onSetShortcutPanel(value) {
        return dispatch(setShowShortcutPanel(value));
    },
    onUnregisterShortcut(shortcuts, handler) {
        dispatch(unregisterShortcut(shortcuts, handler));
    },
});

const mapStateToProps = state => (
    {
        hasChanges: selectHasDirtyState(state),
        lastLocation: selectLastLocation(state),
        lastService: selectLastService(state),
        shortcutPanelIsVisible: selectShortcutPanelIsVisible(state),
        applications: applicationsSelector(state),
    }
);

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(withRouter(FooterContainer));

export {
    filterApplications as _filterApplications,
    FooterContainer as _FooterContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
