import React from 'react';
import PropTypes from 'prop-types';
import Modal from '../Modal/Modal';
import modalStyles from '../Modal/ModalStyles';
import InformationSymbol from '@webui-one/styles/static/InformationSymbol';
import style from './InformationButtonStyles';

class InformationButton extends React.Component {
    constructor(props) {
        super(props);
        this.showModal = this.showModal.bind(this);
        this.hideModal = this.hideModal.bind(this);
        this.state = {
            showModal: false,
        };
    }

    showModal() {
        this.setState({ showModal: true });
    }

    hideModal() {
        this.setState({ showModal: false });
    }

    render() {
        const { color, info } = this.props;
        const { showModal } = this.state;
        return (<div>
            <button onClick={this.showModal} style={style.button}>
                <InformationSymbol color={color} />
            </button>
            {showModal && <Modal onRequestClose={() => this.hideModal()}>
                {info.map(chunk => (<div key={chunk.header}>
                    <div style={modalStyles.header}>
                        {`${chunk.header}: `}
                    </div>
                    <div style={modalStyles.bread}>{chunk.content}</div>
                </div>))}
            </Modal>}
        </div>);
    }
}

InformationButton.propTypes = {
    color: PropTypes.string,
    info: PropTypes.arrayOf(PropTypes.object),
};

InformationButton.defaultProps = {
    info: {},
};

export default InformationButton;
